/* PLay Fair & poppins google font */
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: 'Playfair';
}
button{
  height: 38px;
  width: 130px;
  border: 1px solid white;
  background-color: #cb5c00;
  font-family: 'poppins';
  letter-spacing: 1.2px;
  font-size: 15px;
  font-weight: 500;
  color: #fff;
  border-radius:30px ;
  box-shadow: 2px 2px 3px #ffffff94 , insert 3px 3px 10px #ffffff39;
  transition: 1s;
  cursor: pointer;
}

button:hover{
  box-shadow: 0px 0px 0px #ffffff94 , insert 3px 3px 10px #ffffff39;
  transform: scale(0.95);
}
.section{
  padding: 90px 15%;
  display: flex;
  flex-direction: column;
  gap:40px ;

}
.section_Title{
  font-size: 38px;
  color: #1e1e1e;
}
.section_Title span{
  color: #9d7e54;
}
.section_Heading{
  font-size: 20px;
  font-weight: 600;
  color: #1e1e1e;
  font-family: 'poppins';
}